body {
  background-color: #0d1b1e;
  margin: 0;
  padding: 0;
  font-family: Quicksand, sans-serif;
}

h1 {
  color: #fff;
  font-family: Quicksand, sans-serif;
  margin-bottom: 0;
  text-transform: lowercase;
}

h2 {
  color: #fff;
  font-size: 16px;
  margin: 0;
}

img {
  height: auto;
  width: 100%;
}

input {
  background-color: #0d1b1e;
  border: 1px solid #fff;
  color: #fff;
  margin-bottom: 40px;
  padding: 7px 10px 5px;
}

input:focus {
  border-color: #eba20e;
  outline: none;
}

.wrapper {
  margin: 0 auto;
  padding: 100px 10px;
  width: 580px;
}

.header img {
  display: block;
  padding: 0 0 20px;
  width: 120px;
}

@media screen and (max-width: 813px) {
  .wrapper {
    padding: 50px 0;
    width: 500px;
  }
}

@media screen and (max-width: 412px) {
  .wrapper {
    padding: 50px 0;
    width: 320px;
  }
  
  .header {
    padding-left: 20px;
  }
  
  .station-container {
    border-radius: 0;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    height: 450px;
    margin: 20px auto;
    width: 320px;
  }
  
  .station-image {
    height: auto;
    width: 320px;
  }

  .station-body {
  -ms-flex-pack: end;
      justify-content: flex-end;
  padding-left: 10px;
  width: 320px;
  }
  
  .empty-station {
    background-repeat: no-repeat;
    background-image: 
      -webkit-linear-gradient(white 300px, transparent 0),
      -webkit-linear-gradient(white 55px, transparent 0),
      -webkit-linear-gradient(lightgray 100%, transparent 0);
    background-image: 
      -o-linear-gradient(white 300px, transparent 0),
      -o-linear-gradient(white 55px, transparent 0),
      -o-linear-gradient(lightgray 100%, transparent 0);
    background-image: 
      linear-gradient(white 300px, transparent 0),
      linear-gradient(white 55px, transparent 0),
      linear-gradient(lightgray 100%, transparent 0);
    background-position:
      20px 20px,  /* image */
      30px 370px, /* player */
      0 0;        /* station */
    background-size:
      280px 280px,  /* image */
      260px 55px, /* player */
      320px 450px;  /* station */
    border-radius: 0;
    height: 450px;
    margin: 20px auto;
    width: 320px;
  }
  
}



