.empty-station {
  background-repeat: no-repeat;
  background-image: 
    -webkit-linear-gradient(white 100px, transparent 0),
    -webkit-linear-gradient(white 45px, transparent 0),
    -webkit-linear-gradient(lightgray 100%, transparent 0);
  background-image: 
    -o-linear-gradient(white 100px, transparent 0),
    -o-linear-gradient(white 45px, transparent 0),
    -o-linear-gradient(lightgray 100%, transparent 0);
  background-image: 
    linear-gradient(white 100px, transparent 0),
    linear-gradient(white 45px, transparent 0),
    linear-gradient(lightgray 100%, transparent 0);
  background-position:
    10px 10px,  /* image */
    130px 55px, /* player */
    0 0;        /* station */
  background-size:
    100px 100px,  /* image */
    300px 45px, /* player */
    450px 120px;  /* station */
  border-radius: 0 25px 25px 0;
  content: "";
  height: 120px;
  margin: 20px;
  overflow: hidden;
  position: relative;
  width: 450px;
}

.empty-station:after {
  -webkit-animation: loading 1.5s infinite;
          animation: loading 1.5s infinite;
  background: -webkit-gradient(linear, left top, right top, from(transparent), color-stop(lightgrey), to(transparent));
  background: -webkit-linear-gradient(left, transparent, lightgrey, transparent);
  background: -o-linear-gradient(left, transparent, lightgrey, transparent);
  background: linear-gradient(90deg, transparent, lightgrey, transparent);
  content: '';
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
  
@-webkit-keyframes loading {
  0% {
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
}
  
@keyframes loading {
  0% {
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
}

.station-container {
  border-radius: 0 25px 25px 0;
  display: -ms-flexbox;
  display: flex;
  height: 120px;
  margin: 20px;
  width: 450px;
}

.station-image {
  height: auto;
  width: 120px;
}

.station-body {
display: -ms-flexbox;
display: flex;
-ms-flex-direction: column;
    flex-direction: column;
-ms-flex-pack: justify;
    justify-content: space-between;
padding: 15px 0 15px 15px;
width: 310px;
}

.station-name {
  padding-bottom: 20px;
}



